<script>
import { Doughnut } from "vue-chartjs";


export default {
  extends: Doughnut,
  mounted() {
    // Overwriting base render method with actual data.
    console.log('hi');
    this.renderChart({
      labels: ["In Stock", "Not in Stock"],
      datasets: [
        {
          label: "GitHub Commits",
          backgroundColor: ["green", "red"],
          data: [
            this.$store.getters.inStockNum,
            (this.$store.getters.toysNum - this.$store.getters.inStockNum),
          ],
        },
      ],
    });
  },
};
</script>

