<template>
  <div class="toy-edit">
    <h3>Edit the Toy</h3>
    <div class="edit-from">
      <form @submit.prevent="saveToy">
        <input
          type="text"
          maxlength="20"
          v-model="toy.name"
          placeholder="toy name:"
        />
        <input type="number" v-model="toy.price" placeholder="price" />
        <input type="checkbox" id="stock" name="in-stock" value="inStock" v-model="toy.inStock" />
        <label for="inStock">In Stock</label>
        <button>Save</button>
      </form>
    </div>
  </div>
</template>

<script>
import { toyService } from "../services/toy.service.js";
export default {
  name: "toy-edit",
  data() {
    return {
      toy: {
        name: "",
        price: "",
      },
    };
  },
  created() {
    this.getToyById();
  },
  methods: {
    getToyById() {
      var { toyId } = this.$route.params;

      if (toyId) {
        this.$store.dispatch({ type: "getToyById", toyId }).then((toy) => {
          var currToyCopy = JSON.parse(JSON.stringify(toy));
          this.toy = currToyCopy;
        });
      } else {
        this.toy = toyService.getEmptyToy();
      }
    },
    saveToy() {
      let toy = this.toy;
      //   let name = toy.name;
      if (!toy._id) {
        this.$store.dispatch({ type: "addToy", toy });
        //   .then(toy => {
        //     // showMsg(`Toy Added with ID:${toy._id}`)
        //   })
      } else {
        this.$store.dispatch({ type: "updateToy", toy });
        //   .then(toy => {
        //     // showMsg(`Todo updated`)
        //   })
      }
      this.$router.push("/toy-app");
    },
  },
};
</script>

