import Axios from 'axios'
var axios = Axios.create({
    withCredentials: true
})

// const TOY_URL = '//localhost:3030/api/toy/'

const TOY_URL = (process.env.NODE_ENV !== 'development')
    ? '/api/toy'
    : '//localhost:3030/api/toy';

export const toyService = {
    query,
    getById,
    remove,
    save,
    getEmptyToy
}

// const labels = ["On wheels", "Box game", "Art", "Baby", "Doll", "Puzzle", "Outdoor"]


// _createToys()

// TODO: support paging and filtering and sorting
async function query() {
    // return storageService.query(KEY)
    //  return axios.get(TOY_URL).then(res => res.data)
    const res = await axios.get(TOY_URL)
    return res.data
}

async function getById(id) {
    // return storageService.get(KEY, id)
    // return axios.get(TOY_URL + id).then(res => res.data)
    const res = await axios.get(TOY_URL + id)
    return res.data
}

async function remove(id) {
    // return storageService.remove(KEY, id)
    // return axios.delete(TOY_URL + id).then(res => res.data)
    const res = await axios.delete(TOY_URL + id)
    return res.data
}

async function save(toy) {
    // const savedToy = (toy._id) ? storageService.put(KEY, toy) : storageService.post(KEY, toy)
    // return savedToy;
    if (toy._id) {
        console.log('i am here');
        // return axios.put(TOY_URL, toy).then(res => res.data)
        const res = await axios.put(TOY_URL + toy._id, toy)

        return res.data
    } else {
        // return axios.post(TOY_URL, toy).then(res => res.data)
        const res = await axios.post(TOY_URL, toy)
        // console.log(res.data);
        return res.data
    }
}

function getEmptyToy(name = '', price = 100) {
    return {
        _id: '',
        name,
        price,
        labels: [],
        createdAt: null,
        inStock: false,
        review: 'good toy'
    }
}

