<template>
  <div class="toy-details">
    <section v-if="toy" class="toy-details-container">
      <h1>ID:{{ toy._id }}</h1>
      <h2>NAME:{{ toy.name }}</h2>
      <h3>PRICE:{{ toy.price }}</h3>
      <article>
        <h1>Review:</h1>
        <br />
        {{ toy.review }}
      </article>
      <p v-if="toy.inStock">In Stock</p>
    </section>
    <section class="reviews">
      <ul class="review-list">
        <li v-for="review in reviews" :key="review._id">
          <h3>{{ review.txt }}</h3>
          <p>
            By
            <!-- <router-link :to="`user/${review.byUser._id}`"> -->
            {{ review.byUser.fullname }}
            <!-- </router-link> -->
          </p>
          <hr />
        </li>
      </ul>
      <hr />
      <form v-if="loggedInUser" @submit.prevent="addReview()">
        <h2>Your review of this game:</h2>
        <!-- <select v-model="reviewToEdit.aboutUserId">
          <option v-for="user in users" :key="user._id" :value="user._id">
            {{ user.fullname }}
          </option>
        </select> -->
        <textarea
          placeholder="Your Opinion Matters..."
          v-model="reviewToEdit.txt"
        ></textarea>
        <button>Save</button>
      </form>
    </section>
  </div>
</template>

<script>
import { toyService } from "../services/toy.service.js";
export default {
  name: "toy-details",
  data() {
    return {
      toy: null,
      reviewToEdit: {
        txt: "",
        aboutGameId: null,
      },
    };
  },
  created() {
    const { toyId } = this.$route.params;
    toyService.getById(toyId).then((toy) => {
      this.toy = toy;
      this.reviewToEdit.aboutGameId = toyId;
    });

    this.$store.dispatch({type: 'loadReviews'})

  },
  computed: {
    reviews() {
      return this.$store.getters.reviews.filter( (review) => {
        return review.aboutGameId === this.$route.params.toyId
      }
      );
    },
    loggedInUser() {
      return this.$store.getters.loggedinUser;
    },
  },
  methods: {
    async addReview() {
      await this.$store.dispatch({
        type: "addReview",
        review: this.reviewToEdit,
      });
      this.reviewToEdit = { txt: "", aboutGameId: null };
    },
  },
};
</script>

