<template>
  <div class="container about">
    <p>{{ msg }}</p>

    <div v-if="loggedinUser">
      <h3>
        Loggedin User:
        {{ loggedinUser.username }}
        <button @click="doLogout">Logout</button>
      </h3>
<span>Your reviews adon:</span>
        <ul class="review-list">
        <li v-for="review in reviews" :key="review._id">
          <h3>{{ review.txt }}</h3>
          <p>
            By
            <!-- <router-link :to="`user/${review.byUser._id}`"> -->
            {{ review.byUser.fullname }}
            <!-- </router-link> -->
          </p>
          <hr />
        </li>
      </ul>


    </div>
    <div v-else>
      <h2>Login</h2>
      <form @submit.prevent="doLogin">
        <!-- <select v-model="loginCred.username">
          <option value="">Select User</option>
          <option v-for="user in users" :key="user._id" :value="user.username">{{user.fullname}}</option>
        </select> -->
        <input
          type="text"
          v-model="loginCred.username"
          placeholder="User name"
        />
        <input
          type="text"
          v-model="loginCred.password"
          placeholder="Password"
        />
        <button>Login</button>
      </form>
      <p class="mute">user1 or admin, pass:123</p>
      <form @submit.prevent="doSignup">
        <h2>Signup</h2>
        <input
          type="text"
          v-model="signupCred.fullname"
          placeholder="Your full name"
        />
        <input
          type="text"
          v-model="signupCred.password"
          placeholder="Password"
        />
        <input
          type="text"
          v-model="signupCred.username"
          placeholder="Username"
        />
        <button>Signup</button>
      </form>
    </div>
    <hr />
    <details v-if="isAdmin">
      <summary>Admin Section</summary>
      <ul>
        <li v-for="user in users" :key="user._id">
          <pre>{{ user }}</pre>
          <button @click="removeUser(user._id)">x</button>
        </li>
      </ul>
    </details>
  </div>
</template>

<script>
export default {
  name: "test",
  data() {
    return {
      msg: "",
      loginCred: { username: "user1", password: "123" },
      signupCred: { username: "", password: "", fullname: "" },
      isAdmin: this.evalAdmin()
    };
  },
  computed: {
    users() {
      return this.$store.getters.users;
    },
    loggedinUser() {
      return this.$store.getters.loggedinUser;
    },
      reviews() {
      return this.$store.getters.reviews.filter( (review) => {
        return review.byUserId === this.$store.getters.loggedinUser._id
      }
      );
    }
  },
  created() {
    this.loadUsers();
  },
  methods: {
    evalAdmin(){
      let loggedUser = this.$store.getters.loggedinUser;
      if(loggedUser) return loggedUser.isAdmin
      return false;
    },
    async doLogin() {
      if (!this.loginCred.username || !this.loginCred.password) {
        this.msg = "Please enter username/password";
        return;
      }
      try {
        await this.$store.dispatch({ type: "login", userCred: this.loginCred });
        this.$router.push("/");
      } catch (err) {
        console.log(err);
        this.msg = "Failed to login";
      }
    },
    doLogout() {
      this.$store.dispatch({ type: "logout" });
      this.isAdmin = false;
    },
    async doSignup() {
      if (
        !this.signupCred.fullname ||
        !this.signupCred.password ||
        !this.signupCred.username
      ) {
        this.msg = "Please fill up the form";
        return;
      }
      await this.$store.dispatch({ type: "signup", userCred: this.signupCred });
      this.$router.push("/");
    },
    loadUsers() {
      this.$store.dispatch({ type: "loadUsers" });
    },
    async removeUser(userId) {
      try {
        await this.$store.dispatch({ type: "removeUser", userId });
        this.msg = "User removed";
      } catch (err) {
        this.msg = "Failed to remove user";
      }
    },
  },
};
</script>