
<template>
  <div class="toy-filter">
    <p>Name:</p>
    <!-- <input type="text" v-model="filterBy.name" @input="setFilter" /> -->
    <el-input type="text" placeholder="Search Game" v-model="filterBy.name"  @input="setFilter"></el-input>
    <p>Status:</p>
    <!-- <select v-model="filterBy.status" @change="setFilter">
      <option value="all">All</option>
      <option value="inStock">In Stock</option>
      <option value="notInStock">Not in Stock</option>
    </select> -->
    <el-select
      v-model="filterBy.status"
      @change="setFilter"
      placeholder="Select"
    >
      <el-option
        v-for="item in filterOptions"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      >
      </el-option>
    </el-select>
    <input
      list="labels"
      v-model="filterBy.label"
      id="label-choices"
      name="label-choice"
    />
    <datalist id="labels" @change="setFilter">
      <option value="Doll"></option>
      <option value="Action"></option>
      <option value="Battery"></option>
    </datalist>
    Sort By:
      <el-select
      v-model="filterBy.sortBy"
      @change="setFilter"
      placeholder="Sort By"
    >
      <el-option
        v-for="item in sortOptions"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      >
      </el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  name: "toy-preview",
  props: {
    toy: Object,
  },
  data() {
    return {
      filterBy: {
        title: "",
        status: "all",
        label: "",
        sortBy: "name",
      },
      filterOptions: [
        {
          value: "all",
          label: "All",
        },
        {
          value: "inStock",
          label: "In Stock",
        },
        {
          value: "notInStock",
          label: "Not In Stock",
        },
      ],
      sortOptions:[
        {
          value: "name",
          label: "Name",
        },{
          value: "price",
          label: "Price",
        },{
          value: "created",
          label: "Creation",
        },
      ]
    };
  },
  methods: {
    setFilter() {
      this.$emit("filter", { ...this.filterBy });
    },
  },
};
</script>


