<template>
  <div class="toy-details">
    <p class="toy-filter">Filter By:</p>
    <toy-filter @filter="setFilter" />
     <router-link to="/toy/edit">Add new Toy</router-link> 
    <p v-if="isLoading">Loading ....</p>
    <toy-list v-else :toys="getToys" @remove="removeToy"></toy-list>
    <!-- <div>
      <button @click="prevPage">Prev Page</button>
      <button @click="nextPage">Next Page</button>
    </div> -->
  </div>
</template>

<script>
// import { toyService } from "../services/toy.service.js";
import toyList from "../components/toy-list.vue"
import toyFilter from "../components/toy-filter.vue"
export default {
  name: "toy-app",
  data() {
    return {};
  },
  created() {},
  methods: {
    removeToy(toyId) {
      this.$store
        .dispatch({ type: "removeToy", toyId })
        .then(() => {
          //   showMsg("Todo Removed");
        })
        .catch(() => {
          //   showMsg("Failed to remove Todo", "danger");
        });
    },
    setFilter(filterBy) {
      this.$store.commit({ type: "setFilter", filterBy });
    },
  },
  computed: {
    getToys() {
      return this.$store.getters.getFilteredToys;
    },
    isLoading() {
      //   console.log(this.$store.getters.isLoading)
      return this.$store.getters.isLoading;
    },
  },
  components:{
      toyList,
      toyFilter
  }
};
</script>

