import Vue from 'vue'
import Vuex from 'vuex'
import { toyService } from '../services/toy.service.js'
import { userStore } from './user.store.js'
import { reviewStore } from './review.store.js'


Vue.use(Vuex)

export default new Vuex.Store({
  strict: true,
  state: {
    toys: [],
    isLoading: false,
    filterBy: {
      name: '',
      status: 'all',
      label: '',
      sortBy: 'name'
    }
  },
  mutations: {
    setLoading(state, { isLoading }) {
      state.isLoading = isLoading
    },
    setToys(state, { toys }) {
      state.toys = toys
    },
    addToy(state, { toy }) {
      state.toys.push(toy)
    },
    updateToy(state, { toy }) {
      const idx = state.toys.findIndex((currToy) => currToy._id === toy._id)
      state.toys.splice(idx, 1, toy)
    },
    setFilter(state, { filterBy }) {
      state.filterBy = filterBy
    },
    removeToy(state, { toyId }) {
      const idx = state.toys.findIndex(toy => toy._id === toyId)
      // state.lastRemovedTodo = state.todos[idx]
      state.toys.splice(idx, 1)
    }
  },
  actions: {
    async loadToys({ commit }) {

      commit({ type: 'setLoading', isLoading: true })
      try {
        const toys = await toyService.query()
        commit({ type: 'setToys', toys })
      } catch (err) {
        console.log('error while loading', err);
        throw err
      }
      finally {
        commit({ type: 'setLoading', isLoading: false })
      }
    },
    getToyById(context, { toyId }) {
      return toyService.getById(toyId)
    },
    async addToy({ commit }, { toy }) {
      try{
        toy.createdAt = Date.now()
        const savedToy = await toyService.save(toy)
        commit({ type: 'addToy', toy: savedToy })
     
        return savedToy;
      }catch(err){
        console.log('error while adding a new toy ',err);
        throw err
      }
   
    },
    async updateToy({ commit }, { toy }) {
      try{
        toy.createdAt = Date.now()
        const savedToy = await toyService.save(toy)
        commit({ type: 'updateToy', toy: savedToy })
        return savedToy;
      }catch(err){
        console.log('error while updating a toy ',err);
        throw err
      }
   
    },
    async removeToy({ commit }, { toyId }) {
      try {
        await toyService.remove(toyId)
        commit({ type: 'removeToy', toyId })
      } catch (err) {
        console.log('error while removing', err);
        throw err
      }
    }
  },
  getters: {
    isLoading({ isLoading }) { return isLoading },
    inStockNum(state) {
      var inStockCount = 0;
      state.toys.forEach(toy => {
        if (toy.inStock) inStockCount++
      })
      return inStockCount
    },
    toysNum(state) {
      return state.toys.length
    },
    getFilteredToys(state) {
      let name = state.filterBy.name
      let status = state.filterBy.status
      let label = state.filterBy.label

      let filteredToys = state.toys.filter((currToy) => {
        if (!name && status === 'all') return currToy
        if (name) {
          currToy = (currToy.name.toLowerCase().includes(name.toLowerCase())) ? currToy : false
        }
        if (!currToy) return currToy
        if (label)
          currToy = (currToy.labels.includes(label)) ? currToy : false
        if (!currToy) return currToy
        if (status === 'inStock') {

          return (currToy.inStock)
        }
        else if (status === 'notInStock') return !(currToy.inStock)
        else return currToy

      })
      // if (state.filterBy.pageIdx !== undefined) {
      //   const startIdx = state.filterBy.pageIdx * state.filterBy.page_size
      //   filteredTodos = filteredTodos.slice(
      //     startIdx,
      //     startIdx + state.filterBy.page_size
      //   )
      // }
      return filteredToys.sort((toy1, toy2) => {
        let sortBy = state.filterBy.sortBy
        if (sortBy === 'name') {
          var nameA = toy1.name.toUpperCase(); // ignore upper and lowercase
          var nameB = toy2.name.toUpperCase(); // ignore upper and lowercase
          if (nameA < nameB) {
            return -1;
          }
          if (nameA > nameB) {
            return 1;
          }

          // names must be equal
          return 0;
        }
        if (sortBy === 'price') {
          return toy1.price - toy2.price
        }
        if (sortBy === 'created') {
          return toy1.createdAt - toy2.createdAt
        }
      })
    }
  }
  ,
  modules: {
    userStore,
    reviewStore
  }
})
